html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

.buttonHover {
  cursor: pointer;
}

.containerFix {
  margin-left: -15px;
  margin-right: -15px;
  padding-right: -15px;
}

.contactBox {
  z-index: 99;
}

.nav-bar {
  position:fixed;
  width: 100%;
  min-height: 6vh;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between; 
  align-items: center;
  background-color: rgb(23,46,124);
  /* background-color: rgb(0, 195, 255, 0.5); */
  transition: all .7s ease-in;

  z-index: 99;

  left:0;
  right:0;
}

.nav-items {
  margin-left: auto;
  margin-right: auto;

  font-size: 24px;
  color:white;
}

.nav-item {
  cursor: pointer;
  padding: 20px;
  color: white;
}

.nav-item:hover {
  cursor: pointer;
  padding-bottom: 50px;
  color: white;
  text-decoration: none;
}

/* DROP DOWN MENU */

.dropDownMenu {
  position: relative;
  display: inline;
}

.dropDown {
  /* background-color: #4CAF50; */
  color: white;
  /* padding: 10px; */
  /* font-size: 16px; */
  border: none;
  display: inline;
}

.dropdown-content {
  display: none;
  position: absolute;
  color: rgba(23,46,124);
  background-color: #f1f1f1;
  width:300px;;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  right: -80px;
  
  height: 0;
  opacity:0;
  transition: all .5s ease;

  margin-top: 20px;
}

/* .dropdown-content:hover .dropLink {background-color: #ddd;} */
.dropDown:hover .dropdown-content {
  display: block;

  position: absolute;

  height: 100%;
  opacity: 1;
  transform: translateY(0);
  background-color: white;
}
.dropDown:hover .dropLink {background-color: white;}
.dropDown:hover .dropLink:hover {background-color: rgb(231, 231, 231);}

.dropLink {
  color: black;
  text-decoration: none;
  display: block;
  padding: 30px;
}

