#contactModalBox {
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 50px;
  padding-right: 100px;

  background-color: white;
  border-radius: 20px;
  display: fixed;

  position: fixed;
  bottom: -150vh;
  transition: all 150ms east-in-out;

  top:50%;
  left:50%;
  transform: translate(-50%,-50%);

  width: 50%;
  height: 50%;
  margin-top: 20px;

  box-shadow: inset 0px 0px 5px rgba(0,0,0,0.16), 0px 0px 5px rgba(0,0,0,0.45);

  z-index:99;
}


